import FindDpi from '../../../libs/finddpi/FindDpi';
import { PlainUtils } from '../../../utils/PlainUtils';

/**
 * 
 */
class HeightUnit {

  /**
   * HeightUnit
   * @param dpi
   */
  constructor({
    dpi,
  } = {}) {
    if (PlainUtils.isNotUnDef(dpi)) {
      this.dpi = dpi;
    } else {
      this.dpi = FindDpi();
    }
  }

  /**
   * DPI
   * @returns {*}
   */
  getDpi() {
    return this.dpi;
  }

  /**
   * 
   */
  getPixel(pt) {
    return pt * this.dpi / 72;
  }

  /**
   * 
   */
  getPoint(px) {
    return 72 * px / this.dpi;
  }

}

export {
  HeightUnit,
};
